*{
    box-sizing: border-box;
    margin:0;
    padding:0;
}

body{
    font-family:'Outfit', 'segoe ui';
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
}

p{
font-size:1.6rem;
}

h1{
    font-size:6rem;
    }

.app{
    width:100%;
    height:100vh;
    position:relative;
    background-color:rgba(0,0,0,0.4);
    color:#fff;
}   

.app:before {
    content:'';
    background:url('./assets/background.jpg') no-repeat center center/cover;
    position:absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    z-index: -1;
    }

.container{
    max-width:700px;
    height:700px;
    margin:auto;
    padding: 0 1em;
    position:relative;
    top:10%;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    }
    
.app .search{
    text-align:center;
    padding:1rem;
    }

.app input{
    padding:.7rem 1.5rem;
    font-size:1.2rem;
    border-radius:25px;
    border:1px solid rgba(255,255,255,0.8);
    background:rgba(255,255,255,0.1);
    color:#f8f8f8;
    }

::placeholder{
    color:#f8f8f8;

}




.app .main{
    margin: 1rem auto;
}




.bold{
    font-weight:700;
}


.card{
    width:300px;
    text-align:center;
    padding:.7rem 1.5rem;
    font-size:1.2rem;
    border-radius:25px;
    border:1px solid rgba(255,255,255,0.8);
    background:rgba(255,255,255,0.1);
    color:#f8f8f8;
}

.app .country{
font-size:30px;
background-color:#fff;
height:50px;
width:50px;
border-radius:50%;
-moz-border-radius:50%;
-webkit-border-radius:50%;
color:black;
}